.career {
	&-details {
		background: rgba(0,0,0,0.5);
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		height: auto;
		z-index: 9;
		&--list {
			padding: var(--spacing-l) 0;
		}
		&-inner {
			display: flex;
			align-items: center;
   			justify-content: space-between;
			@extend .container;
			padding: 1.5rem 0;
			color: white;
		}
		&-item {
			@extend .container;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			padding: 0 0 var(--spacing) 0;
			margin-top: var(--spacing);
			border-bottom: 1px solid var(--color-grey-mid);
			&--content {
				width: 50%;
				ul li {
					// list-style: disc;
					padding-bottom: 8px;
					line-height: 27px;
					border-bottom: 0 !important;
				}
			}
		}
		&--additional {
			text-align: center;
			padding-top: var(--spacing-l);
		}
	}
}
.career-details--list {
	.accordion-list {
		max-width: var(--container-l);
	}
	.accordion--content {
		flex-wrap: wrap;
		.career-details-item--content {
			margin-left: 0;
			@include mq(medium) {
				margin-left: 50%;
			}
		}
		&__image {
			img {
				margin-bottom: 2rem;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
}